import React from 'react'
import { useToasts } from 'react-toast-notifications'
import { CONSTANT } from '../common'

function Banner () {
	const { addToast } = useToasts();
	const clickButton = (type) => {
		if (CONSTANT.SOCIAL_LINK[type]) {
			window.open(CONSTANT.SOCIAL_LINK[type])
		}
	}
	const copyAddress = () => {
		const copyText = document.getElementById("address_bsc");
		copyText.select();
		copyText.setSelectionRange(0, 99999); /* For mobile devices */
		document.execCommand("copy");
		addToast("Copied" ,{
			appearance: 'success',
			autoDismiss: true,
		})
	}
  return (
		<div id="" className="banner">
		<div className="container-fluid">
			<div className="row">
				<div className="col-12">
					<div className="banner__content">
						<div className="banner__content-title">Plant 2 Earn</div>
						<div className="banner__content-subtitle">Plant warrior - Fighting an Earn $P2E</div>
					</div>
					<div className="banner__box">
						<div className="banner__box-item ellipse" onClick={() => { clickButton(CONSTANT.BUTTON_TYPE.BUYP2E) }}>
							<div className="item__content">Buy P2E</div>
						</div>
						<div className="banner__box-item ellipse" onClick={() => { clickButton(CONSTANT.BUTTON_TYPE.WHITEPAPER) }}>
							<div className="item__content">Whitepaper</div>
						</div>
					</div>
					<div className="banner__box" onClick={() => { clickButton(CONSTANT.BUTTON_TYPE.ENTER_DAPP) }}>
						<div className="banner__box-item polygon">
							<div className="item__content">Enter DApp</div>
						</div>
					</div>
					<div className="banner__input">
						<label htmlFor="address_bsc">Address:</label>
						<div className="banner__input-item">
							<input style={{cursor: 'pointer'}} readOnly type="text" id="address_bsc" value={CONSTANT.SOCIAL_LINK[CONSTANT.CONTRACT_ADDRESS] ? CONSTANT.SOCIAL_LINK[CONSTANT.CONTRACT_ADDRESS] : ''}/>
							<button className="item__button" onClick={copyAddress}>
								<img src={`${process.env.PUBLIC_URL}/images/layouts/copy.png`} alt="icon"/>
							</button>
						</div>
					<div className="banner__input-button">
                  <button onClick={() => { clickButton(CONSTANT.CHECK_BSC) }}>Check on BSCScan</button>
                </div>
					</div>
				</div>
			</div>
		</div>
	</div>
	)
}

export default Banner
