/* eslint-disable no-script-url */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import { CONSTANT } from '../common';
const { SOCIAL_TYPE } = CONSTANT;
function Footer() {
  const handleClickSocial = (type) => {
    if (CONSTANT.SOCIAL_LINK[type]) {
      window.open(CONSTANT.SOCIAL_LINK[type]);
    }
  };
  return (
    <div className="footer">
      <div className="container custom">
        <div className="footer__content">
          <div className="footer__content-text">Join Our Commnunity</div>
          <div className="footer__content-social">
            <a
              onClick={() => handleClickSocial(SOCIAL_TYPE.TWITTER)}
              href="javaScript:Void(0);"
              className="social__link"
            >
              <img
                src={`${process.env.PUBLIC_URL}/images/layouts/twitter-white.png`}
                alt="icon"
              />
            </a>
            <a
              onClick={() => handleClickSocial(SOCIAL_TYPE.TELEGRAM_GROUP)}
              href="javaScript:Void(0);"
              className="social__link"
            >
              <img
                src={`${process.env.PUBLIC_URL}/images/layouts/telegram-white.png`}
                alt="icon"
              />
            </a>
            <a
              onClick={() => handleClickSocial(SOCIAL_TYPE.GIT)}
              href="javaScript:Void(0);"
              className="social__link"
            >
              <img
                src={`${process.env.PUBLIC_URL}/images/layouts/github-white.png`}
                alt="icon"
              />
            </a>
            <a
              onClick={() => handleClickSocial(SOCIAL_TYPE.DISCORD)}
              href="javaScript:Void(0);"
              className="social__link"
            >
              <img
                src={`${process.env.PUBLIC_URL}/images/layouts/discord-white.png`}
                alt="icon"
              />
            </a>
          </div>
          <div className="footer__content-text">
            Email Support: info@plant2earn.io
          </div>
        </div>
      </div>
    </div>
  );
}

export default Footer;
