/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect } from 'react'
import twiterLogo from '../images/layouts/twitter-black.png'
import teleLogo from '../images/layouts/telegram-black.png'
import gitLogo from '../images/layouts/github-black.png'
import discordLogo from '../images/layouts/discord-black.png'
import logoApp from '../images/layouts/logo.png'
import { CONSTANT } from '../common'
const { SOCIAL_TYPE } = CONSTANT
function Header () {
  useEffect(() => {
    let header = document.querySelector('.header')
    window.addEventListener('scroll', () => {
      header.classList.toggle('sticky', window.scrollY > 0)
    })
  
    //===== Click Toggle Menu =====//
    let body = document.querySelector('body')
    let menuList = document.querySelector('.header__menu-content')
    let button = document.querySelector('.header__menu-button')
    button.addEventListener('click', () => {
      button.classList.toggle('animate')
      body.classList.toggle('overflow-hidden')
      menuList.classList.toggle('show')
    })
  }, [])

  const handleClickSocial = (type) => {
    if(CONSTANT.SOCIAL_LINK[type]) {
      window.open(CONSTANT.SOCIAL_LINK[type])
    }
  }
  const clickMenu = (type) => {
    document.querySelector('.header__menu-content').classList.remove('show')
    document.querySelector('.header__menu-button').classList.remove('animate')
    if (CONSTANT.SOCIAL_LINK[type]) {
      window.open(CONSTANT.SOCIAL_LINK[type])
    }
  }
    return (
        <div className="header">
        <div className="container-fluid custom">
          <div className="header__menu">
            <div className="header__menu-logo"><a className="logo__link" href="/"><img src={logoApp} alt="logo"/></a></div>
            <div className="header__menu-button">
              <div className="button__icon"></div>
            </div>
            <div className="header__menu-content">
              <div className="content__list">
                <div className="content__list-item" onClick={clickMenu}><a className="item__link" href="#">Home</a></div>
                <div className="content__list-item" onClick={() => handleClickSocial(SOCIAL_TYPE.TOKENOMICS)}><a className="item__link" href="javaScript:Void(0);">Tokenomics</a></div>
                <div className="content__list-item" onClick={() => handleClickSocial(SOCIAL_TYPE.ROADMAP)}><a className="item__link" href="javaScript:Void(0);">Roadmap</a></div>
                <div className="content__list-item" onClick={() => clickMenu(CONSTANT.MENU_TYPE.COMMUNITY)}><a className="item__link" href="#">Community</a></div>
                <div className="content__list-item" onClick={() => handleClickSocial(SOCIAL_TYPE.AUDIT)}><a className="item__link" href="#">Audit</a></div>
              </div>
              <div className="content__social">
								<a className="content__social-item" onClick={() => handleClickSocial(SOCIAL_TYPE.TWITTER)} href="javaScript:Void(0);">
									<img src={twiterLogo} alt="icon"/>
								</a>
								<a className="content__social-item" onClick={() => handleClickSocial(SOCIAL_TYPE.TELEGRAM_GROUP)} href="javaScript:Void(0);">
									<img src={teleLogo} alt="icon"/>
								</a>
								<a className="content__social-item" onClick={() => handleClickSocial(SOCIAL_TYPE.GIT)} href="javaScript:Void(0);">
									<img src={gitLogo} alt="icon"/>
								</a>
								<a className="content__social-item" onClick={() => handleClickSocial(SOCIAL_TYPE.DISCORD)} href="javaScript:Void(0);">
									<img src={discordLogo} alt="icon"/>
								</a>
							</div>
            </div>
          </div>
        </div>
      </div>
    )
}

export default Header